




import zh_EN from '@/common/zh_EN.js'
import zh_CN from '@/common/zh_CN.js'

let toast;

function formatTime(time) {
    if (typeof time !== 'number' || time < 0) {
        return time
    }

    var hour = parseInt(time / 3600)
    time = time % 3600
    var minute = parseInt(time / 60)
    time = time % 60
    var second = time

    return ([hour, minute, second]).map(function(n) {
                n = n.toString()
                return n[1] ? n : '0' + n
            }).join(':')
}

function formatLocation(longitude, latitude) {
    if (typeof longitude === 'string' && typeof latitude === 'string') {
        longitude = parseFloat(longitude)
        latitude = parseFloat(latitude)
    }

    longitude = longitude.toFixed(2)
    latitude = latitude.toFixed(2)

    return {
        longitude: longitude.toString().split('.'),
        latitude: latitude.toString().split('.')
    }
}

var dateUtils = {
    UNITS: {
        '': 31557600000,
        '': 2629800000,
        '': 86400000,
        '': 3600000,
        '': 60000,
        '': 1000
    },
    humanize: function(milliseconds) {
        var humanize = ''
        for (var key in this.UNITS) {
            if (milliseconds >= this.UNITS[key]) {
                humanize = Math.floor(milliseconds / this.UNITS[key]) + key + ''
                break
            }
        }
        return humanize || ''
    },
    format: function(dateStr) {
        var date = this.parse(dateStr)
        var diff = Date.now() - date.getTime()
        if (diff < this.UNITS['']) {
            return this.humanize(diff)
        }
        var _format = function(number) {
            return (number < 10 ? ('0' + number) : number)
        }
        return date.getFullYear() + '/' + _format(date.getMonth() + 1) + '/' + _format(date.getDay()) + '-' +
            _format(date.getHours()) + ':' + _format(date.getMinutes())
    },
    //"yyyy-mm-dd HH:MM:ss"Date
    parse: function(str) {
        var a = str.split(/[^0-9]/)
        return new Date(a[0], a[1] - 1, a[2], a[3], a[4], a[5])
    }
}

/**
 * H5 
 * */
const copyText = (domname, text) => {
	if(uni.getStorageSync('language') == 'en'){
		toast = zh_EN.toasts.copyText
	}else{
		toast = zh_CN.toasts.copyText
	}
	
    //  .length selectText 
    const textString = text.toString()
    let input = ''

    if (domname) {
        input = document.querySelector(domname)
    }

    if (!input) {
        input = document.createElement('input')
        input.id = 'copy-input'
        input.readOnly = 'readOnly' // ios
        input.style.position = 'fixed'
        input.style.left = '-1000px'
        input.style.top = '-1000px'
        input.style.zIndex = '-1000'
        input.style.border = '1px #fff solid'
        input.style.opacity = '0'
        document.body.appendChild(input)
    } else {
        input.readOnly = 'readOnly'
    }

    input.value = textString
    // ios input.select();
    selectText(input, 0, textString.length)

    if (document.execCommand('copy')) {
        document.execCommand('copy')
        uni.showToast({
            title: toast.success,
            duration: 1500,
            icon: 'none'
        })
    }

    // 
    input.blur()

    function selectText(textbox, startIndex, stopIndex) {
        if (textbox.createTextRange) { //ie
            const range = textbox.createTextRange()
            range.collapse(true)
            range.moveStart('character', startIndex) // 
            range.moveEnd('character', stopIndex - startIndex) // 
            range.select() // 
        } else { //firefox/chrome
            textbox.setSelectionRange(startIndex, stopIndex)
            textbox.focus()
        }
    }
}

function share(me) {
    if (document.addEventListener) {
        document.addEventListener('WeixinJSBridgeReady', onBridgeReady(me), false)
    }
}

function onBridgeReady(me) {
  
    var shareHref = ''
    if (me.shareHref == '') {
        shareHref = window.location.href
    } else {
        shareHref = me.shareHref
    }
    var wdesc = ''
    var wtit = ''
    var wappid = ''
    var wimg = me.LaiKeTuiCommon.LKT_ROOT_VERSION_URL + 'images/icon1/kfef2x.png'
    // 
    WeixinJSBridge.on('menu:share:appmessage', function(argv) {
        WeixinJSBridge.invoke('sendAppMessage', {
            'appid': wappid,
            'img_url': wimg,
            'img_width': '200',
            'img_height': '200',
            'link': shareHref,
            'desc': wdesc,
            'title': wtit,
        })
    })
    // 
    WeixinJSBridge.on('menu:share:timeline', function(argv) {
        WeixinJSBridge.invoke('shareTimeline', {
            'img_url': wimg,
            'img_width': '200',
            'img_height': '200',
            'link': shareHref,
            'desc': wdesc,
            'title': wtit
        })
    })
    // 
    WeixinJSBridge.on('menu:share:weibo', function(argv) {
        WeixinJSBridge.invoke('shareWeibo', {
            'content': '',
            'url': shareHref,
        })
    })
}

const jssdk_share = function(me, option) {
    var me = this

    var now_url = window.location.href
    var can_share = false

    var data = {
        module: 'app',
        action: 'jssdk',
        m: 'getData',
        url: now_url
    }

    uni.request({
        data,
        url: uni.getStorageSync('url'),
        header: {
            'content-type': 'application/x-www-form-urlencoded'
        },
        method: 'POST',
        success: (res) => {
            var jsApiList = ['updateAppMessageShareData', 'updateTimelineShareData']

            jweixin.config({
                debug: false, // ,apialertpclogpc
                appId: res.data.appId, // 
                timestamp: res.data.timestamp, // 
                nonceStr: res.data.nonceStr, // 
                signature: res.data.signature, // 
                jsApiList: ['updateAppMessageShareData', 'updateTimelineShareData'] // JS
            })
        },
    })

    jweixin.ready(() => {
        // configready
        // config
        // configready
        // ready

        jweixin.updateAppMessageShareData({
            title: '', // 
            desc: '', // 
            link: now_url, // JS
            imgUrl: laiketuiComm.LKT_ROOT_VERSION_URL + 'images/share_img.png', // 
            success: function() {}
        })
    })

}

/**
 * @description 
 * @param { time }
 * */
const getTimeDiff = (time) => {
    
    // app
    time = new Date(time.replace(/-/g, '/'))
    
    // 
    let currentTime = new Date()

    let reslut = time.getTime() - currentTime.getTime()

    //
    let days = Math.floor(reslut / (24 * 3600 * 1000))

    //
    let leave1 = reslut % (24 * 3600 * 1000) //
    let hours = Math.floor(leave1 / (3600 * 1000))

    //
    let leave2 = leave1 % (3600 * 1000) //
    let minutes = Math.floor(leave2 / (60 * 1000))

    //
    let leave3 = leave2 % (60 * 1000) //
    let seconds = Math.floor(leave3 / 1000)

    if (hours.toString().length == 1) {
        hours = '0' + hours
    }

    if (minutes.toString().length == 1) {
        minutes = '0' + minutes
    }

    if (seconds.toString().length == 1) {
        seconds = '0' + seconds
    }

    return {
        days,
        hours,
        minutes,
        seconds
    }
}

/**
 * 
 * @returns {number}
 */
const getStatusBarHeight = () => {
    let statusBarHeight = 0
    uni.getSystemInfo({
        success: async res => {
            statusBarHeight = res.statusBarHeight
        }
    })
    return statusBarHeight
}

/**
 * 
 * @param delay
 * @returns {Promise<unknown>}
 */
async function later(delay) {
    return new Promise(function(resolve) {
        setTimeout(resolve, delay);
    });
}

/**
 * https://www.jianshu.com/p/fdbf293d0a85
 * uuidGlobally Unique Identifier, uuid(Universally Unique IDentifier)
 * ,,v-for,index
 * item"",
 * v-for,idindex
 * @param {Number} len uuid
 * @param {Boolean} firstU "u"
 * @param {Nubmer} radix uuid(),2-,8-,10-,16-
 */
function guid(len = 32, firstU = true, radix = null) {
    let chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
    let uuid = [];
    radix = radix || chars.length;

    if (len) {
        // uuid,,0|x,x,
        for (let i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
    } else {
        let r;
        // rfc4122uuid,
        uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
        uuid[14] = '4';

        for (let i = 0; i < 36; i++) {
            if (!uuid[i]) {
                r = 0 | Math.random() * 16;
                uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
            }
        }
    }
    // ,u,,guuididclass
    if (firstU) {
        uuid.shift();
        return 'u' + uuid.join('');
    } else {
        return uuid.join('');
    }
}

module.exports = {
    formatTime,
    formatLocation,
    dateUtils,
    copyText,
    share,
    jssdk_share,
    getTimeDiff,
    getStatusBarHeight,
    later,
    guid
}
